<template>
    <div>
        <b-card>
            <template #header>
                <h2 class="mb-0">Change Password</h2>
            </template>
            <b-card-body>
                <b-form @submit.prevent="changeMyPass()">
                    <b-row >
                        <b-col sm="6" class="mb-3">
                            <label>Current Password</label>
                            <b-form-input type="password" placeholder="Current Password" v-model="info.current_password"></b-form-input>
                        </b-col>
                        <b-col sm="6" class="mb-3">
                            <label>New Password</label>
                            <b-form-input type="password" placeholder="New Password" v-model="info.new_password"></b-form-input>
                        </b-col>
                        <b-col sm="6" class="mb-3">
                            <label>Confirm New Password</label>
                            <b-form-input type="password" placeholder="Confirm New Password" v-model="info.new_password_confirmation"></b-form-input>
                        </b-col>
                        <b-col sm="6" class="mb-3">
                            <div class="d-flex pt-4">
                                <b-button variant="success" type="submit" class="ml-auto mt-4">
                                    <b-spinner small type="grow"  v-if="loading.main"></b-spinner>
                                    <span v-else>Submit</span>
                                </b-button>
                            </div>
                        </b-col>
                    </b-row>
                </b-form>
            </b-card-body>
        </b-card>
    </div>
</template>
<script>
import axios from 'axios'
import {mapState} from 'vuex'
import {showToast, resetProperties} from '@/assets/js/index';
export default {
    name:'AdminProfile',
    data() {
        return {
            info:{},
            loading:{
                main:false
            }
        }
    },
    methods:{
        changeMyPass() {
            this.loading.main = true;
            axios.post(`admin/v1/users/change-password`, this.info)
            .then(res=>{
                console.log(res)
                this.loading.main = false
                if(typeof res.data == 'string' ) {
                    showToast('danger', 'Error', res.data, this)
                } else {
                    showToast('success', 'Success', 'Password changed successfully', this)
                    this.info = resetProperties(this.info)
                }
            })
            .catch(()=>{
                this.loading.main = false;
                showToast('danger', 'Error', 'something went wrong', this)
            })
        }
    },

}
</script>